<template>
   
    <v-dialog activator="parent" v-model="localDialog" max-width="500">
              <template v-slot:default="{ isActive }">

                <v-progress-linear
                  color="cyan"
                  indeterminate
                  v-if="isLoading"
                ></v-progress-linear>

                <v-card rounded="lg" class="to-blackMode">
                  <v-card-title class="d-flex justify-space-between align-center">

                    <div class="text-h5 text-medium-emphasis ps-2">
                     <span style="color: white;">   </span>
                    </div>
    
                    <v-btn  
                      variant="text"
                      @click="isActive.value = false"
                      :disabled="isLoading"
                    ></v-btn>

                  </v-card-title>
    
                  <v-divider class="mb-4"></v-divider>
    
                  <v-card-text>
                    <v-file-input
                      
                      label="  "
                      prepend-icon="mdi-camera"
                      variant="filled"
                      accept="image/*"
                      @change="handleFileEvent"
                      :disabled="isLoading || resizeLoading"
                      @click:clear="imageToNull"
                    ></v-file-input> 
                    <v-progress-linear
                  color="cyan"
                  indeterminate
                  v-if="resizeLoading"
                ></v-progress-linear>
                   
                        <div class="info-text">
                                     .
                        </div>
                                
                   
                  </v-card-text>
    
                  <v-divider class="mt-2"></v-divider>
    
                  <v-card-actions class="my-2 d-flex justify-end">
                    <v-btn
                      class="text-none"
                      rounded="xl"
                      text="Cancel"
                      @click="closeDialog()"
                       :disabled="isLoading ||resizeLoading"
                    ></v-btn>
    
                    <v-btn
                      class="text-none"
                      color="primary"
                      rounded="xl"
                      text="Send"
                      variant="flat"
                      @click="changeProfile()"
                      :disabled="isLoading || resizeLoading"
                    ></v-btn>
                  </v-card-actions>
                </v-card>
              </template>
            </v-dialog>
    
    </template>
    
    <script>
import api from '@/api';

    export default {
      name: 'ChangeProfileImg',
    
    
      props: {
        value: {//     
          type: Boolean,
          required: true
        }
      
    },data(){
      return{
        profileImgFile:null, //  
        isLoading:false ,
        fileSize:0,
        resizeLoading:false    
      }
    }
    ,computed:{
      localDialog:{
          get(){
              return this.value //     (true/false)  , props value   .
          }
      }
    }   
      ,
      methods: {
        //     
        imageToNull(){
          this.profileImgFile=null
          this.fileSize=0
        },
        closeDialog() {
          this.profileImgFile=null
          this.fileSize=0
          this.isLoading=false
          this.resizeLoading=false
          this.$emit('changeProfileImgClose');
        },
         //   
         handleFileEvent(event){
          this.resizeLoading=true;
          const file= event.target.files[0]
          if(!file){
            this.profileImgFile = null;
            this.fileSize=0
            this.resizeLoading=false
            return;
          } 
          //    
         
    
                const reader = new FileReader(); //    

              reader.onload = (e) => {
                
                const img = new Image(); //   
                img.src = e.target.result; //     

                img.onload = () => {
                  const canvas = document.createElement('canvas'); //  
                  const ctx = canvas.getContext('2d'); // 2D  
                  const originalWidth = img.width;
                  const originalHeight = img.height;
                  const targetWidth = 550; //  
                  const targetHeight = Math.round((originalHeight / originalWidth) * targetWidth); //  v
                  canvas.width = targetWidth; 
                  canvas.height = targetHeight;
                  ctx.imageSmoothingEnabled = true; //   
                  ctx.imageSmoothingQuality = 'high'; //   
                  ctx.drawImage(img, 0, 0, targetWidth, targetHeight); //  
                

                  canvas.toBlob((blob) => { //  Blob 
                    if (blob) {
                      const resizedFile = new File([blob], file.name, { type: 'image/jpg' }); //   
                    
                      this.profileImgFile = resizedFile;
                      this.fileSize=resizedFile.size
                      this.resizeLoading=false
                      
                    } else {
                      console.error('Blob  ');
                      this.resizeLoading=false
                    }
                  }, 'image/jpg'); // Blob  
                };

                img.onerror = () => {
                  this.resizeLoading=false
                  console.error('  ');
                };
              };

              reader.onerror = () => {
                this.resizeLoading=false
                console.error('  ');
              };

              reader.readAsDataURL(file); //   
                        //  data  
                      
                  
     },
      
       //  API 
       changeProfile(){

        const MAX_SIZE_MB = 2;
        const MAX_SIZE_BYTES = MAX_SIZE_MB * 1024 * 1024; //    ()
          if(this.resizeLoading){
            alert('  .')
            return
          }
          //  5mb    
          if (this.fileSize > MAX_SIZE_BYTES) {
           
            alert(`   ${MAX_SIZE_MB}MB .   .`);
            return;
          }
        //  
        if(this.isLoading){
        return
        }
        //  FORMDATA  
       const formData= new FormData()
       formData.append('profileImgFile',this.profileImgFile)
       // 
       this.isLoading=true;
       //API  
       api.put('/feed/userfeed',formData)
       .then(()=>{
           alert('   .')
           this.closeDialog()
       })
       .catch((err)=>{
        if(err?.response?.data?.message){
        alert(err?.response?.data?.message)  
        }
       })
       .finally(()=>{
        this.isLoading=false
       })
       } 
      }
    }
    </script>
    
 <style>
.info-text {
  color: grey; 
  font-size: 0.875rem; 
  margin-top: 8px; 
  text-align: center;
}
 </style>